import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { InstituteModel } from '../models/model';

@Injectable({
  providedIn: 'root'
})
export class InstitutionService {
  apiUrl = `${environment.apiUrl}/api/Institutions`;

  constructor(private http: HttpClient) { }

  get() {
    return this.http.get(this.apiUrl);
  }

  getById(id: number) {
    return this.http.get(`${this.apiUrl}/${id}`);
  }

  create(institute: InstituteModel) {
    return this.http.post(this.apiUrl, institute);
  }

  update(institute: InstituteModel) {
    return this.http.put(`${this.apiUrl}/${institute.id}`, institute);
  }

  delete(id: number) {
    return this.http.delete(`${this.apiUrl}/${id}`);
  }
}
