import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { VehicleModel } from '../models/model';

@Injectable({
  providedIn: 'root'
})
export class VehicleService {
  apiUrl = `${environment.apiUrl}/api/Vechiles`;

  constructor(private http: HttpClient) { }

  get(institutionId: number) {
    return this.http.get(`${this.apiUrl}?institutionId=${institutionId}`);
  }

  getById(id: number) {
    return this.http.get(`${this.apiUrl}/${id}`);
  }

  create(vehicle: VehicleModel) {
    return this.http.post(this.apiUrl, vehicle);
  }

  update(vehicle: VehicleModel) {
    return this.http.put(`${this.apiUrl}/${vehicle.id}`, vehicle);
  }

  delete(id: number) {
    return this.http.delete(`${this.apiUrl}/${id}`);
  }
}
