import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { VehicleRouteModel } from '../models/model';

@Injectable({
  providedIn: 'root'
})
export class VechileRouteService {
  apiUrl = `${environment.apiUrl}/api/VechileRoutes`;

  constructor(private http: HttpClient) { }

  get() {
    return this.http.get(this.apiUrl);
  }

  getById(id: number) {
    return this.http.get(`${this.apiUrl}/${id}`);
  }

  create(vehicleRoute: VehicleRouteModel) {
    return this.http.post(this.apiUrl, vehicleRoute);
  }

  update(vehicleRoute: VehicleRouteModel) {
    return this.http.put(`${this.apiUrl}/${vehicleRoute.id}`, vehicleRoute);
  }

  delete(id: number) {
    return this.http.delete(`${this.apiUrl}/${id}`);
  }
}
