import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { PageMasterModel } from '../models/model';

@Injectable({
    providedIn: 'root'
  })

export class PageMasterService {
    apiUrl = `${environment.apiUrl}/api/PageMaster`;

constructor(private http: HttpClient) { }

get(){
    return this.http.get(this.apiUrl);}

getById(id:number){
    return  this.http.get(`${this.apiUrl}/${id}`);
}

create(pageMaster:PageMasterModel){
 return this.http.post(this.apiUrl,pageMaster);
}

update(pageMaster: PageMasterModel) {
    return this.http.put(`${this.apiUrl}/${pageMaster.id}`, pageMaster);
  }

delete(id:number){
    return this.http.delete(`${this.apiUrl}/${id}`)
}

}
